define("buy-button-sdk/metal/uniq", ["exports"], function (exports) {
  "use strict";

  exports["default"] = function (array) {
    return array.reduce(function (uniqueArray, item) {
      if (uniqueArray.indexOf(item) < 0) {
        uniqueArray.push(item);
      }

      return uniqueArray;
    }, []);
  };
});